.StickyNode__contentEditable {
  min-height: 20px;
  border: 0;
  resize: none;
  cursor: text;
  font-size: 24px;
  caret-color: rgb(5, 5, 5);
  display: block;
  position: relative;
  outline: 0;
  padding: 10px;
  user-select: text;
  white-space: pre-wrap;
  word-wrap: break-word;
}
.StickyNode__placeholder {
  font-size: 24px;
  color: #999;
  overflow: hidden;
  position: absolute;
  text-overflow: ellipsis;
  top: 30px;
  left: 20px;
  width: 120px;
  user-select: none;
  white-space: nowrap;
  display: inline-block;
  pointer-events: none;
}
